.nav {
    /*  ширна произвольная, не стесняйтесь экспериментировать */
    width: 320px;
    min-width: 320px;
    /* фиксируем и выставляем высоту панели на максимум */
    height: 10px;
    position: fixed;
    top: 1em;
    bottom: 0;
    margin: 10px;
    /* сдвигаем (прячем) панель относительно левого края страницы */
    left: -320px;
    /* внутренние отступы */
    padding: 15px 20px;
    /* плавный переход смещения панели */
    -webkit-transition: left 0.3s;
    -moz-transition: left 0.3s;
    transition: left 0.3s;
    /* определяем цвет фона панели */
    background: #16a085;
    /* поверх других элементов */
    z-index: 2000;
}

.navToggle {
    /* абсолютно позиционируем */
    position: absolute;
    /* относительно левого края панели */
    left: 320px;
    /* отступ от верхнего края панели */
    top: 5px;
    /* внутренние отступы */
    padding:5px;
    /* определяем цвет фона переключателя
     * чаще вчего в соответствии с цветом фона панели
    */
    background: inherit;
    /* цвет текста */
    color: #dadada;
    /* вид курсора */
    cursor: pointer;
    /* размер шрифта */
    font-size: 1.2em;
    line-height: 1;
    /* всегда поверх других элементов страницы */
    z-index: 2001;
    /* анимируем цвет текста при наведении */
    -webkit-transition: color .25s ease-in-out;
    -moz-transition: color .25s ease-in-out;
    transition: color .25s ease-in-out;
}

.navToggle:after {
    content: '\2630';
    text-decoration: none;
}

/* цвет текста при наведении */

.navToggle:hover {
    color: #f4f4f4;
}

[id='navToggle'] {
    position: absolute;
    display: none;
}

[id='navToggle']:checked ~ .nav > .nav-toggle {
    left: auto;
    right: 2px;
    top: 1em;
}

[id='navToggle']:checked ~ .nav {
    left: 0;
    box-shadow:4px 0 20px 0 rgba(0,0,0, 0.5);
    -moz-box-shadow:4px 0 20px 0 rgba(0,0,0, 0.5);
    -webkit-box-shadow:4px 0 20px 0 rgba(0,0,0, 0.5);
    overflow-y: auto;
}

[id='navToggle']:checked ~ main > article {
    -webkit-transform: translateX(320px);
    -moz-transform: translateX(320px);
    transform: translateX(320px);
}

[id='navToggle']:checked ~ .nav > .nav-toggle:after {
    content: '\2715';
}


